body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: start;
}

.loader {
  -webkit-animation: loaderRotation infinite 1s linear;
          animation: loaderRotation infinite 1s linear;
  display: flex;
  justify-content: center;
  align-items: center; }

@-webkit-keyframes loaderRotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes loaderRotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.game__wrapper {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  align-items: center;
  justify-content: center; }

.game__wheel {
  position: absolute;
  right: 0;
  bottom: 0; }

.game__menu-button-wrapper {
  position: absolute;
  right: 0;
  top: 0; }

.game__menu-button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  padding: 1em;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: black; }
  .game__menu-button:hover {
    background-color: rgba(0, 0, 0, 0.2); }

.map-tile {
  fill: rgba(0, 0, 0, 0.8);
  opacity: 0;
  stroke: none;
  -webkit-animation: mapTileFade 1s forwards;
          animation: mapTileFade 1s forwards; }

@-webkit-keyframes mapTileFade {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes mapTileFade {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.map__wrapper {
  display: block;
  height: 100vmin;
  width: 100vmin;
  position: relative; }
  @media (min-width: 800px) {
    .map__wrapper {
      width: 100%; } }

.map__svg {
  display: block;
  height: 100vmin;
  width: 100vmin; }
  @media (min-width: 800px) {
    .map__svg {
      width: 100%; } }

.map__background {
  height: 100%; }

.map-path {
  fill: none;
  stroke-width: 40;
  stroke: white; }
  .map-path--red {
    fill: none;
    stroke-width: 20;
    stroke: rgba(255, 50, 50, 0.6); }

.map-cross {
  opacity: 0;
  -webkit-animation: mapCrossFadeIn 0.4s ease 1s forwards;
          animation: mapCrossFadeIn 0.4s ease 1s forwards;
  cursor: pointer; }
  .map-cross__path {
    fill: #cc242f;
    stroke: none; }

@-webkit-keyframes mapCrossFadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes mapCrossFadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.wheel__wrapper {
  padding: 1em;
  width: calc(200px + 2em);
  height: calc(200px + 2em); }

.wheel__plate {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer; }

.wheel__compass {
  max-width: 100%;
  max-height: 100%;
  background: transparent; }

.modal__dimmer {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.87);
  opacity: 0;
  align-items: center;
  justify-content: center; }

.menu-modal__wrapper {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background-color: black;
  min-width: 200px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.2em; }

.menu-modal__title {
  color: #fff;
  font-size: 1.2em;
  margin-bottom: 1em; }

.menu-modal__button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  padding: 0.5em;
  background-color: #1a1a1a;
  color: #fff;
  border: 1px solid #fff;
  margin: 0.5em 0;
  cursor: pointer;
  text-align: center; }
  .menu-modal__button:hover {
    background-color: black; }

.question-modal__wrapper {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background-color: black;
  min-width: 400px;
  max-width: 80vmin;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.2em; }

.question-modal__category {
  color: #fff;
  font-size: 1.2em;
  font-weight: 500;
  margin-bottom: 0.5em; }

.question-modal__question-wrapper {
  margin-bottom: 1em;
  display: flex;
  flex-direction: row; }

.question-modal__counter {
  flex: none;
  width: 2em;
  height: 2em;
  margin-left: 1em; }

.question-modal__question {
  flex: 1 1 auto;
  color: #fff;
  font-size: 1.2em;
  text-align: center;
  line-height: 1.5; }

.question-modal__buttons {
  display: flex;
  align-items: row; }

.question-modal__button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  display: flex;
  flex: 1 1 auto;
  padding: 0.5em;
  background-color: #1a1a1a;
  color: #fff;
  border: 1px solid #fff;
  cursor: pointer;
  text-align: center; }
  .question-modal__button:hover {
    background-color: black; }

.counter__wrapper {
  width: 2em;
  height: 2em;
  position: relative; }

.counter__svg {
  position: absolute;
  width: 2em;
  height: 2em;
  -webkit-transform: rotateZ(-90deg);
          transform: rotateZ(-90deg); }

.counter__circle {
  fill: none;
  stroke: #c33c54;
  stroke-width: 0.5; }

.counter__digit {
  width: 2em;
  height: 2em;
  color: #c33c54;
  text-align: center;
  line-height: 2em; }

.finish-modal__wrapper {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background-color: black;
  min-width: 200px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.2em; }

.finish-modal__title {
  color: #fff;
  font-size: 1.2em;
  margin-bottom: 1em; }

.finish-modal__button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  padding: 0.5em;
  background-color: #1a1a1a;
  color: #fff;
  border: 1px solid #fff;
  margin: 0.5em 0;
  cursor: pointer;
  text-align: center; }
  .finish-modal__button:hover {
    background-color: black; }

.no-questions-modal__wrapper {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background-color: black;
  min-width: 200px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.2em; }

.no-questions-modal__title {
  color: #fff;
  font-size: 1.2em;
  margin-bottom: 1em; }

.no-questions-modal__button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  padding: 0.5em;
  background-color: #1a1a1a;
  color: #fff;
  border: 1px solid #fff;
  margin: 0.5em 0;
  cursor: pointer;
  text-align: center; }
  .no-questions-modal__button:hover {
    background-color: black; }

.home__section {
  max-width: 100%;
  width: 800px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

.home__button {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  border: none;
  background-color: #c33c54;
  color: #fff;
  font-size: 1.2em;
  padding: 1em;
  cursor: pointer; }
  .home__button:hover {
    background-color: #cf6376; }

.map-background {
  z-index: -1;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center; }
  .map-background__svg {
    width: 100%;
    height: 100%;
    display: block; }
  .map-background__coordinate {
    fill: none;
    stroke: rgba(0, 0, 0, 0.2);
    stroke-width: 1;
    stroke-miterlimit: 10; }

.app__container {
  margin: 0;
  padding: 0;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column; }

